@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;1,300;1,400&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App {
  background-color: #00b2ff;
  display: grid;
  font-family: "Roboto Mono", monospace;
  grid-template-areas:
    "header nav"
    "sidebar main"
    "footer main";
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 1fr 5fr;
  height: 100vh;
}

.header {
  grid-area: header;
}

.nav {
  grid-area: nav;
}

.sidebar {
  grid-area: sidebar;
}

.footer {
  grid-area: footer;
}

.main-area {
  grid-area: main;
}

.welcome-container{display:flex;flex-direction:column;padding:20px}.welcome-header{color:#EFEFEF}

.header-container{background-color:#EFEFEF;display:flex;position:relative;z-index:1;flex-direction:row-reverse;padding:50px 20px}.header-container:before{content:"";position:absolute;left:19%;bottom:0;width:82%;border-bottom:2px solid #0078BF}.title{color:#0078BF}.covid-live{font-size:40px}.colorado{font-size:28px;text-align:right}

.sidebar-container{background-color:#EFEFEF}.sidebar-links{list-style-type:none;padding-right:25px;margin:20px 0;text-align:right;text-decoration:none}.sidebar-links a{text-decoration:none}.about,.staying-protected,.local-health,.county-stats,.bookmarked-counties{color:#0788bf;font-size:1.2em;margin:40px 0 0 10px;padding-right:5px}.active-link{border-right:2px solid #00B2FF;margin-left:10px}.inactive-link{border-right:2px solid #EFEFEF;margin-left:10px}

.nav-container{background-color:#00B2FF;display:flex;position:relative;z-index:1}.nav-container:before{content:"";position:absolute;left:0;bottom:0;width:88%;border-bottom:2px solid #EFEFEF}.site-description{color:#EFEFEF;font-size:20px;font-style:italic;margin:auto 0 auto 8%}

.footer-container{background:#EFEFEF;padding-bottom:1.2%;text-align:center}.copyright{font-size:12px}.github-link{color:black}

.about-container{display:flex;flex-direction:column;padding:20px}.about-header{color:#EFEFEF}

.staying-protected-container{display:flex;flex-direction:column;padding:20px}.staying-protected-header{color:#EFEFEF}

.bookmarks-container{display:flex;flex-direction:column;padding:20px;overflow:scroll}.bookmarks-header{color:#EFEFEF}

table,th,tr{border:1px solid black;margin-top:20px}.county-data-container{display:flex;margin:20px auto 0 auto}

.local-health-container{display:flex;flex-direction:column;padding:20px}.local-health-header{color:#EFEFEF}.county-picker{display:flex;flex-direction:row}

#counties{background-color:#00B2FF;color:#EFEFEF;border:2px solid #EFEFEF;font-family:"Roboto Mono", monospace;font-size:1em;padding:10px;text-decoration:none;width:220px;padding:55px}.select{display:block}

.county-stats-container{display:flex;flex-direction:column;padding:20px}.county-stats-header{color:#EFEFEF}.county-picker{display:flex;flex-direction:row}.bookmark-btn{background-color:#00B2FF;color:#000;border:2px solid #000;font-family:"Roboto Mono", monospace;font-size:1em;padding:10px;text-decoration:none;width:220px;margin:50px auto}

